<template lang="pug">

footer.footer
	.footer-item
		.footer-item-title 
		.footer-tel
			span   :
			span +7 (923) 674-56-34
		.footer-tel
			span   :
			span +7 (923) 674-56-34
		.footer-tel
			span   -:
			span +7 (923) 674-56-34
		.footer-mail
			span :
			span qsdfwer@dasd.com
		.footer-btn
			.footer-btn-mail
			.footer-btn-vk
			.footer-btn-ok
	.footer-item.footer-item-map(:class="{active: activeMap, fixed: fixedMap}")
		.footer-map-fade(@click="hideMap")
		.footer-map(:style="styles")
			.footer-map-fullscreen(@click="showMap")  
			yandexMap(:coords="coords" zoom="11" :controls="mapControls" :options="{autoFitToViewport: 'always'}")
				ymap-marker(v-for="marker in markers" :coords="marker.coords" :marker-id="marker.id" :key="marker.id" :callbacks="{click: showBaloon}")
	.footer-item
		.footer-item-title 
		.footer-address(v-for="(address, i) in addresses" :key="i")
			.footer-subtitle {{address.name}}
			.footer-address-name(v-for="item in address.list") {{item}}

</template>

<script>
import { yandexMap, ymapMarker} from 'vue-yandex-maps'

export default {
	mounted(){
		this.mapStyles.width = this.map.offsetWidth + 'px',
		this.mapStyles.height = this.map.offsetHeight + 'px'
		this.resize()
	},
	data () {
		return {
			addresses: [
				{
					name: ' ',
					list: [
						'. , 89,  , 1 ',
						'. , 89,  , 1 ',
						'. , 89,  , 1 '
					]
				},
				{
					name: '',
					list: [
						'. , 89,  , 1 ',
						'. , 89,  , 1 '
					]
				}
			],
			markers: [
				{
					id: 1,
					name: 'asd',
					address: '. , 63',
					coords: [56.00866120437235, 92.86809117533872],
				},
				{
					id: 2,
					name: 'qwe',
					address: '. , 63',
					coords: [56.00866120437235, 92.80809117533872],
				},
			],
			mapControls: [
				'searchControl',
				'trafficControl',
				'zoomControl',
				'geolocationControl'
			],
			pos: {
				top: 0,
				left: 0
			},
			activeBaloon: false,
			activeMap: false,
			fixedMap: false,
			mapStyles: {
				width: '100%',
				height: '100%',
				left: 0,
				top: 0
			}
		}
	},
	methods: {
		showBaloon(e){
			this.active = e.originalEvent.target.properties._data.markerId
			e = e.originalEvent.domEvent.originalEvent
			this.activeBaloon = true
			this.pos.left = e.layerX
			this.pos.top = e.layerY
		},
		hideBaloon(){
			this.activeBaloon = false
		},
		showMap(){
			this.resize()
			this.fixedMap = true
			this.activeMap = true
		},
		hideMap(){
			this.activeMap = false
			setTimeout(() => this.fixedMap = false, 300)
		},
		resize(){
			var pos = this.map.getBoundingClientRect();
			this.mapStyles.top = pos.top + 'px'
			this.mapStyles.left = pos.left + 'px'
		}
	},
	computed: {
		baloon(){
			return this.markers.find(item => item.id == this.active)
		},
		coords(){
			let y = 0, x = 0
			for (let i in this.markers){
				y += this.markers[i].coords[0]
				x += this.markers[i].coords[1]
			}
			return [y/this.markers.length, x/this.markers.length]
		},
		map(){
			return this.$el.querySelector('.footer-map')
		},
		styles(){
			return this.mapStyles
		}
	},
	components: {
		yandexMap,
		ymapMarker
	},
}
</script>

<style lang="stylus">

.footer
	position relative
	width 1080px
	margin 50px auto
	display flex
	justify-content space-between
	&-item
		background-color #fff
		box-shadow 0 0 6px 0 rgba(#000, .1)
		padding 12px 20px 18px
		border-radius 10px
		width 334px
		font-size 14px
		position relative
		&-title
			text-align center
			font-weight 600
			margin-bottom 10px
		&-map
			padding 0
			min-height 220px
			background transparent
			box-shadow none
			&.fixed
				.footer-map
					position fixed
					z-index 201
					overflow hidden
					&-fade
						position fixed
						z-index 200
						width 100%
						height 100%
						left 0
						top 0
						cursor pointer
			&.active
				.footer-map
					top 50%!important
					top 50vh!important
					left 50%!important
					left 50vw!important
					transform translate(-50%, -50%)
					width 1080px!important
					height 70%!important
					height 70vh!important
					.ymap-container
						width 1070px!important
						height 100%!important
						height calc(70vh - 10px)!important
					&-fullscreen
						top -30px
					&-fade
						background-color rgba(#000, .2)
	&-map
		height 100%
		width 100%
		left 0
		top 0
		background-color #fff
		box-shadow 0 0 6px 0 rgba(#000, .1)
		border-radius 10px
		transition all .3s
		&-fade
			background-color rgba(#000, 0)
			transition background-color .3s
		&-fullscreen
			position absolute
			z-index 2
			left 50%
			top 15px
			transform translateX(-50%)
			font-size 12px
			background-color #fff
			padding 3px 5px
			border-radius 3px
			box-shadow 0 1px 2px 1px rgba(0,0,0,.15),0 2px 5px -3px rgba(0,0,0,.15)
			cursor pointer
			transition top .3s
		.ymap-container
			position: absolute
			z-index 1
			height calc(100% - 10px)
			width calc(100% - 10px)
			left 5px
			top 5px
			border-radius: 5px
			overflow: hidden

</style>
