<template lang="pug">

.popup-container(v-if="popup" :class="{active: active}")
	.fade(@click="activePopup = null")
	v-popup-map(v-if="popup == 'map'")
	v-popup-auth(v-if="popup == 'auth'")
	v-popup-info(v-if="popup == 'info'")

</template>

<script>
import iPopupMap from './map.vue'
import iPopupAuth from './auth.vue'
import iPopupInfo from './info.vue'

export default {
	data () {
		return {
			active: true,
			popup: 'info'
		}
	},
	watch: {
		activePopup(val) {
			if (val) {
				this.popup = val
				this.active = true
			}
			else {
				this.active = false
				setTimeout(() => {
					this.popup = null
				}, 600)
			}
		}
	},
	computed:{
		activePopup:{
			get(){
				return this.$store.state.popup
			},
			set(val){
				this.$store.commit('setPopup', val)
			}
		}
	},
	components: {
		'v-popup-map': iPopupMap,
		'v-popup-auth': iPopupAuth,
		'v-popup-info': iPopupInfo
	},
}

</script>

<style lang="stylus" scoped>

.popup
	z-index 2
	position absolute
	width 400px
	height 500px
	top -100%
	top -100vh
	left 50%
	left 50vw
	transform translate(-50%, -50%)
	background-color #fff
	box-shadow 0 0 10px 0 rgba(#000, .2)
	border-radius 20px
	animation hidePopup .6s
	&-container
		position fixed
		top 0
		left 0
		width 100%
		height 100%
		width 100vw
		height 100vh
		z-index 200
		.fade
			position absolute
			width 100%
			height 100%
			top 0
			left 0
			z-index 1
			background-color #000
			cursor pointer
			opacity 0
			animation fadeOut .5s
		&.active
			.fade
				opacity .2
				animation fadeIn .5s
			.popup
				top 50%
				top 50vh
				animation showPopup .8s

@keyframes fadeOut
	0%
		opacity .2
	100%
		opacity 0
@keyframes fadeIn
	0%
		opacity 0
	100%
		opacity .2
@keyframes hidePopup
	0%
		margin-top 0
		top 50%
		top 50vh
	30%
		margin-top 35px
		top 50%
		top 50vh
	100%
		margin-top 0
		top -100%
		top -100vh
@keyframes showPopup
	0%
		margin-top 0
		top -100%
		top -100vh
	60%
		margin-top 45px
		top 50%
		top 50vh
	100%
		margin-top 0
		top 50%
		top 50vh


</style>