<template lang="pug">

.minicart(:class="{hidden: hidden}")
	.minicart-count(:class="{active: count>0}") {{count}}
	.minicart-container
		.minicart-nav
			.minicart-icon(@click="hidden = false")
			.minicart-sum {{sum + '' | priceMask}}  / {{count}} .
			.minicart-close(@click="hidden = true")
		.minicart-list
			.minicart-list-empty(v-if="count == 0")   
			v-minicard(v-for="prod in cart" :key="prod.id" :prod="prod")


</template>

<script>
import iMiniCard from './minicard.vue'

export default {
	data () {
		return {
			hidden: true
		}
	},
	computed:{
		cart() {
			return this.$store.state.catalog.prods.cart
		},
		count() {
			return this.$store.state.catalog.prods.cart.length
		},
		sum() {
			return this.$store.state.catalog.server.sum
		}
	},
	components: {
		'v-minicard': iMiniCard,
	},
}

</script>

<style lang="stylus">

.minicart
	position fixed
	z-index 100
	right 20px
	bottom 20px
	width 330px
	height 330px
	background-color #fff
	border-radius: 20px
	box-shadow: 1px 2px 6px 0 rgba(#000, .1)
	transition all .3s
	overflow visible
	&-count
		background-color #ff2400
		font-size 12px
		position absolute
		top 0
		right 0
		color #fff
		width 15px
		height 15px
		line-height 15px
		border-radius 100%
		text-align center
		font-weight 600
		z-index 4
		display none
	&-container
		overflow hidden
		position relative
		width 100%
		height 330px
		border-radius 20px
	&-nav
		height 40px
		width 330px
		box-shadow: 0 1px 5px 0px rgba(#000, .2)
		position relative
		border-top-left-radius: 20px
		border-top-right-radius: 20px
		background-color #fff
		z-index 2
	&-list
		position relative
		width 330px
		height 290px
		&-empty
			font-size 16px
			position absolute
			top 50%
			left 0
			width 100%
			transform translateY(-50%)
			text-align center
			padding-bottom 30px
			font-weight 600
			color #999999
	&-icon
		background-image url(/img/cart.svg)
		background-size 22px
		background-repeat no-repeat
		background-position center 60%
		width 38px
		height 38px
		border-radius: 100%
		background-color #fff
		cursor pointer
		position absolute
		left 5px
		top 1px
		transition all .3s
		z-index 3
	&-sum
		font-size 14px
		font-weight 600
		line-height 40px
		position absolute
		top 0
		left 50%
		transform: translateX(-50%)
		color #444444
	&-close
		background-image url(/img/close.svg)
		background-repeat no-repeat
		background-size contain
		width 16px
		height 16px
		top 12px
		right 12px
		position absolute
		cursor pointer
		opacity 0.3
	&.hidden
		width 50px
		height 50px
		border-radius 50px
		.minicart-container
			width 50px
			height 50px
			border-radius 50px
		.minicart-count.active
			display block
		.minicart-icon
			background-color #fff
			width 50px
			height 50px
			top 0
			left 0
			background-size 25px
			background-position center 50%

</style>
