<template lang="pug">

.popup.popup-map(:class="{open: open}")
	.fade(@click="hideBaloon")
	.baloon(:style="{top: pos.top +'px', left: pos.left + 'px'}")
		.baloon-address {{baloon.address}}
		.baloon-info {{baloon.name}}
	yandexMap(:coords="coords" zoom="13")
		ymap-marker(v-for="marker in markers" :coords="marker.coords" :marker-id="marker.id" :key="marker.id" :callbacks="{click: showBaloon}")

</template>

<script>
import { yandexMap, ymapMarker } from 'vue-yandex-maps'

export default {
	data () {
		return {
			markers: [
				{
					id: 1,
					name: 'asd',
					address: '. , 63',
					coords: [56.00866120437235, 92.86809117533872],
				},
				{
					id: 2,
					name: 'qwe',
					address: '. , 63',
					coords: [56.00866120437235, 92.80809117533872],
				},
			],
			open: false,
			pos: {
				top: 0,
				left: 0
			},
			active: 1,
			mapControl: {
				fullscreenControl: false
			}
		}
	},
	methods: {
		showBaloon(e){
			this.active = e.originalEvent.target.properties._data.markerId
			e = e.originalEvent.domEvent.originalEvent
			this.open = true
			this.pos.left = e.clientX - this.$el.getBoundingClientRect().left
			this.pos.top = e.clientY - this.$el.getBoundingClientRect().top
		},
		hideBaloon(){
			this.open = false
		},
	},
	computed: {
		baloon(){
			return this.markers.find(item => item.id == this.active)
		},
		coords(){
			let y = 0, x = 0
			for (let i in this.markers){
				y += this.markers[i].coords[0]
				x += this.markers[i].coords[1]
			}
			return [y/this.markers.length, x/this.markers.length]
		}
	},
	components: {
		yandexMap,
		ymapMarker
	},
}

</script>

<style lang="stylus" scoped>

.popup.popup-map
	width 1080px
	height 70%
	.ymap-container
		position absolute
		z-index 2
		width calc(100% - 14px)
		height calc(100% - 14px)
		top 7px
		left 7px
		border-radius 15px
		overflow hidden
	.fade
		position absolute
		z-index 1
		width 0
		height 0
		width calc(100% - 14px)
		height calc(100% - 14px)
		top 7px
		left 7px
		border-radius 15px
		cursor pointer
		background-color #000
		opacity 0
		transition opacity .3s, z-index .01s .3s
	.baloon
		position absolute
		z-index 4
		width 6px
		height 6px
		background-color red
		z-index 20
		transform scale(0)
		font-size 14px
		color #ffffff
	&.open
		.fade
			z-index 3
			opacity .2
			transition opacity .3s
		.baloon
			transform scale(1)

</style>