import Axios from "axios"

export default {
	namespaced: true,
	state: () => {
		return {
			prods: [],
			categories: {
				list: [],
				map: {}
			},
			profile: []
		}
	},
	getters: {
		catalog: (state) => {
			return {
				prods: JSON.parse(JSON.stringify(state.prods.sort((a,b)=>a.popular < b.popular).slice(0, 10))),
				categories: JSON.parse(JSON.stringify(state.categories)),
				more: state.prods.length > 10
			}
		}
	},
	mutations: {
		server: (state, { prods, list, map }) => {
			for (let prod of prods)
				state.prods.push(prod)
			state.categories.list = list
			state.categories.map = map
		}
	},
	actions: {
		loadServer: async ({ commit }) => {
			let { data: prods } = await Axios.get('/data/prods.json'),
				{ data: list } = await Axios.get('/data/categories.list.json'),
				{ data: map } = await Axios.get('/data/categories.map.json')
			commit('server', { prods, list, map })
		},
		loadProds: async ({state}, query) => {
			let category = state.categories.list[query.category]
			let prods = state.prods.filter(prod => (
				(query.search == '' || prod.name.toLowerCase().includes(query.search.toLowerCase(),-1))
				&&
				(
					(query.category == 0 && query.path == '')
					||
					(
						state.categories.list[prod.cat_id].id == query.category
						||
						state.categories.list[prod.cat_id].path.includes(category.path + '.' + category.id)
					)
				)
			))
			let sort = query.sortId,
				dir = query.sortDir ? -1 : 1
			prods.sort((a, b) => {
				if (a[sort] > b[sort])
					return 1 * dir
				if (a[sort] < b[sort])
					return -1 * dir
				if (a[sort] == b[sort])
					return 0
			})
			return {
				prods: JSON.parse(JSON.stringify(prods.slice(query.offset * 10, (query.offset+1) * 10))),
				more: prods.length >= (query.offset + 1) * 10
			}
		},
		loadCart: async ({ state }, cart) => {
			cart = JSON.parse(cart)
			let result = []
			for (let item of cart){
				let prod = state.prods.find(prod => prod.id == item[0])
				if (prod.box && item[1] % prod.box == 0) {
					prod.package = 'box'
					prod.count = item[1] / prod.box
				}
				else if (prod.pack && item[1] % prod.pack == 0) {
					prod.package = 'pack'
					prod.count = item[1] / prod.pack
				}
				else if (prod.one) {
					prod.package = 'one'
					prod.count = item[1]
				}
				prod.sum = item[1] * prod.price
				result.push(prod)
			}
			return result
		}
	}
}