import Axios from 'axios'

export default {
	namespaced: true,
	state: {
		profile: {},
		history: [],
		auth: null
	},
	mutations: {
		setAuth: (state, val) => {
			state.auth = val
		},
		logout: (state) => {
			state.personal = {}
			state.history = {}
		},
		loadProfile: (state, e) => {
			state.profile = e.profile
			state.history = e.history
		},
		updateProfile: (state, e) => {
			let profile = state.profile
			for (let i in e)
				profile[i] = e[i]
		}
	},
	actions: {
		login: async ({ commit, dispatch }, { tel, password }) => {
			// const { data: result } = await Axios.post('/api/login', { tel, password })
			let { data: auth } = await Axios.get('/data/auth.json')
			let result = (tel == auth.tel && password == auth.password)
			if (!result) return false
			commit('setAuth', true)
			await dispatch('loadProfile')
			return true
		},
		reg: async () => {
			// const { data: result } = await Axios.post('/api/reg', {
			// 	tel: data.tel,
			// 	password: data.password,
			// 	email: data.email,
			// 	name: data.name,
			// 	surname: data.surname
			// })
			// if (result !== true) return false
			// commit('setAuth', true)
			// dispatch('loadProfile')
			// return true
		},
		logout: ({ commit }) => {
			// Axios.post('/api/logout')
			commit('logout')
			commit('setAuth', false)
		},
		loadProfile: async ({commit}) => {
			let { data } = await Axios.get('/data/profile.json')
			commit('loadProfile', { profile: data.profile, history: data.history })
		},
		updateProfile: async () => {
			// let { data: result } = await Axios.post('/api/profile/update', data)
			// if (result) commit('updateProfile', data)
		}
	}
}