<template lang="pug">

.card
	.card-name {{prod.name}}
	.card-price {{prod.price | priceMask}} P
		span(v-if="prod.count > 0 && prod.package != 'one'")  x {{prod.count}} {{text[prod.package]}} ({{prod[prod.package]}} ) = {{(prod.sum + '') | priceMask}} 
		span(v-if="prod.count > 0 && prod.package == 'one'")  x {{prod.count}} {{text[prod.package]}} = {{(prod.sum + '') | priceMask}} 
	v-calc(:prod="prod" @fCatalog="$emit('fCatalog', $event)")

</template>

<script>
import iCalc from './calc.vue'

export default {
	data(){
		return {
			text: {
				one: '',
				pack: '',
				box: '',
			}
		}
	},
	props: ['prod'],
	components: {
		'v-calc': iCalc,
	},
}

</script>

<style lang="stylus">

.card
	padding 0 20px
	height 45px
	background-color #fff
	border-radius 10px
	box-shadow -0px 0px 6px 0 rgba(#000, .1)
	margin-bottom 10px
	font-size 14px
	display flex
	align-items center
	line-height 1.4
	&:last-child
		margin-bottom 0
	&-name
		width 50%
	&-price
		width 30%
	.calc
		width 20%

</style>
