export default {
	install(Vue) {
		Vue.prototype.format = function(val) {
			if (val)
				return val.split(' ').join('').replace(/[^-0-9]/gim, '') * 1;
		}
		Vue.prototype.mask = function(th) {
			if (!th) return;
			var caret = Vue.getCaretPosition(th),
				val = th.value,
				spacesOld = Vue.spacesCaret(val, caret.start);
			th.value = this.maskVal(val);
			var spacesNew = Vue.spacesCaret(th.value, caret.start);
			Vue.setCaretPosition(th, caret.start + spacesNew - spacesOld, caret.end + spacesNew - spacesOld);
		}

		Vue.prototype.maskSet = function(th, val) {
			if (th.length <= 0) return;
			var caret = Vue.getCaretPosition(th),
				spacesOld = Vue.spacesCaret(val, caret.start);
			th.value = this.maskVal(val);
			var spacesNew = Vue.spacesCaret(th.value, caret.start);
			Vue.setCaretPosition(th, caret.start + spacesNew - spacesOld, caret.end + spacesNew - spacesOld);
		}

		Vue.prototype.maskVal = function(val) {
			val = val.split(' ').join('').replace(/[^-0-9]/gim, '')
			val *= 1
			val += ''
			var end = '';
			for (var i = val.length - 1, j = 0; i >= 0; i--, j++) {
				if (j > 0 && j % 3 == 0) {
					end = ' ' + end;
				}
				end = val[i] + end;
			}
			return end;
		}
		
		Vue.spacesCaret = function(val, pos) {
			var count = 0;
			for (let i = 0; i < pos; i++)
				if (val[i] == ' ') count++;
			return count;
		}


		Vue.getCaretPosition = function(ctrl) {
			if (document.selection) {
				ctrl.focus();
				var range = document.selection.createRange();
				var rangelen = range.text.length;
				range.moveStart('character', -ctrl.value.length);
				var start = range.text.length - rangelen;
				return {
					'start': start,
					'end': start + rangelen
				};
			} else if (ctrl.selectionStart || ctrl.selectionStart == '0') {
				return {
					'start': ctrl.selectionStart,
					'end': ctrl.selectionEnd
				};
			} else {
				return {
					'start': 0,
					'end': 0
				};
			}
		}

		Vue.setCaretPosition = function(ctrl, start, end) {
			if (ctrl.setSelectionRange) {
				ctrl.focus();
				ctrl.setSelectionRange(start, end);
			} else if (ctrl.createTextRange) {
				var range = ctrl.createTextRange();
				range.collapse(true);
				range.moveEnd('character', end);
				range.moveStart('character', start);
				range.select();
			}
		}
	}
}