import Vue from 'vue'
import Vuex from 'vuex'
import ModuleCatalog from 'core/store/catalog'
import ModuleProfile from 'core/store/profile'
import ModuleServer from 'core/store/server'
// import Axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		popup: 'info'
	},
	getters: {
		auth: async (state) => {
			// if (state.profile.auth === null){
			// 	let { data } = await Axios.post('/api/auth')
			// 	state.profile.auth = data
			// }

			// return state.profile.auth
			return state.profile.auth || false
		}
	},
	mutations: {
		setPopup: (state, name) => {
			state.popup = name
		}
	},
	actions: {
		loadServer: async ({getters, dispatch}) => {
			await dispatch('server/loadServer')
			dispatch('catalog/loadCatalog')
			if (await getters.auth) dispatch('profile/loadProfile')
		}
	},
	modules: {
		catalog: ModuleCatalog,
		profile: ModuleProfile,
		server: ModuleServer,
	}
})