<template lang="pug">

header.header
	.container(:class="{fixed: fixed}")
		.logo 
		.nav
			router-link.nav-link(v-for="item in nav" :to="item.url" :key="item.url") {{item.name}}
		.right
			router-link.cart(to="/cart")
				.cart-count(v-if="cart") {{cart}}
			router-link.profile(v-if="$store.state.profile.auth" to="/profile/")
			.profile(v-else @click="$store.commit('setPopup', 'auth')")

</template>

<script>

export default {
	mounted: function(){
		var vue = this;
		fixed();
		window.onscroll = fixed;
		function fixed() {
			var scrolled = window.pageYOffset || document.documentElement.scrollTop;
			if (!vue.fixed && scrolled >= 1)
				vue.fixed = 1;
			else if (scrolled < 1)
				vue.fixed = 0;
		}
	},
	data () {
		return {
			fixed: false,
			nav: [
				{
					name: '',
					url: '/'
				},
				{
					name: ' ',
					url: '/info'
				},
				{
					name: '',
					url: '/contacts'
				},
			]
		}
	},
	computed: {
		cart(){
			return this.$store.state.catalog.prods.cart.length
		},
		scroll(){
			return window.pageYOffset || document.documentElement.scrollTop
		}
	}
}

</script>

<style lang="stylus" scoped>

.header
	height 57px
	position relative
	font-weight 800
	color #333
	
.container
	z-index 100
	top 7px
	left 0
	left calc(25px)
	height 50px
	width 100%
	width calc(100% - 50px)
	width calc(100vw - 50px)
	position fixed
	border-radius: 50px
	transition: background-color .3s, box-shadow .3s
	&.fixed
		background-color #fff
		box-shadow: 0 0px 6px 0 rgba(#000, .1)
	
.logo
	font-size 16px
	line-height 40px
	position absolute
	left 20px
	top 50%
	transform: translateY(-50%)
.nav
	position absolute
	left 50%
	top 50%
	transform: translate(-50%, -50%)
	&-link
		font-size 16px
		margin 0 15px
		display inline-block
		vertical-align top
		cursor pointer
		position relative
		user-select none
		&::after
			content ''
			position absolute
			width 100%
			height 1px
			left 0
			bottom -1px
			opacity 0
			background-color #fde910
			transition opacity .1s
		&.active
			&::after
				opacity 1
.right
	position absolute
	right 20px
	top 50%
	transform: translateY(-50%)
	display flex
	align-items center
.cart
	height 30px
	width 30px
	margin-right 15px
	background-image url('/img/cart.svg')
	background-size 90%
	background-position center
	background-repeat no-repeat
	cursor pointer
	position relative
	&-count
		background-color #ff2400
		font-size 12px
		position absolute
		top -3px
		right -8px	
		color #fff
		width 15px
		height 15px
		line-height 15px
		border-radius 100%
		text-align center
		font-weight 600
		z-index 4
.profile
	height 30px
	width 30px
	background-image url('/img/user.svg')
	background-size 90%
	background-position center
	background-repeat no-repeat
	cursor pointer

</style>