<template lang="pug">

.review#review
	v-flickity.slider(ref="flickity" :options="flickityOptions")
		.item(v-for="item in reviews" :class="item.class")
			.head
				.img(:style="{backgroundImage: 'url(' + item.img + ')'}")
				.name {{item.name}}
			.text {{item.text}}

</template>

<script>
import flickity from 'vue-flickity'

export default {
	mounted(){
		this.activeSlide = 0
		setTimeout(() => this.$refs.flickity.resize(), 300)
	},
	data () {
		return {
			reviews: [
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
				{
					class: null,
					img: '/img/flowers-3.jpg',
					name: ' ',
					text: '-   ,        .  ,  ,                .'
				},
			],
			activeSlide: null,
			flickityOptions: {
				wrapAround: true,
				pageDots: false,
				autoPlay: 5000,
				on: {
					change: () => this.activeSlide = this.$refs.flickity.selectedIndex()
				}
			}
		}
	},
	watch: {
		activeSlide(i){
			let slides = this.reviews,
				l = slides.length

			for (let j in slides)
				slides[j].class = null
			slides[i].class = 'active'

			if (i < l - 1) slides[i + 1].class = 'active'
			else slides[0].class = 'active'
			if (i > 0) slides[i - 1].class = 'active'
			else slides[l - 1].class = 'active'
			if (i > 1) slides[i - 2].class = 'before'
			else slides[l + i - 2].class = 'before'
			if (i < l - 2) slides[i + 2].class = 'after'
			else slides[i + 2 - l].class = 'after'
		}
	},
	components: {
		'v-flickity': flickity
	}
}

</script>

<style lang="stylus" scoped>

.review
	position relative
	margin: 50px auto
.item
	width 334px
	background-color #fff
	box-shadow 0 0 6px 0 rgba(#000, .1)
	padding 15px
	border-radius: 10px
	margin: 10px 20px
	transform: translateX(0)
	opacity 0
	transition transform .5s
	&.active
		opacity 1
	&.after, &.before
		opacity 1
		transition transform .5s, opacity .01s .5s
	&.before
		transform: translateX(calc(-50vw + 580px))
	&.after
		transform: translateX(calc(50vw - 580px))
.img
	width 40px
	height 40px
	border-radius: 100%
	background-size: cover
	background-position center
.head
	position relative
	display flex
	align-items center
.name
	font-size 16px
	font-weight 600
	margin-left 10px
.text
	padding 0 0 5px 50px
	font-size 14px
	color #666666

</style>
