/* eslint-disable no-useless-escape */
// import Axios from 'axios'

export default {
	namespaced: true,
	state: () => {
		return {
			prods: {
				list: [],
				show: [],
				cart: []
			},
			categories: {
				map: [],
				list: []
			},
			server: {
				loaded: null,
				search: '',
				category: 0,
				sum: 0,
				more: true,
				offset: 0,
				sort: {
					id: 'popular',
					dir: true
				}
			}
		}
	},
	getters: {
		cookieCart: () => {
			let matches = document.cookie.match(new RegExp(
				"(?:^|; )" + 'cart'.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
			)),
				cart = matches ? decodeURIComponent(matches[1]) : undefined
			return cart
		}
	},
	mutations: {
		loadCatalog: (state, data) => {
			state.server.more = data.more
			state.categories.list = data.categories.list
			state.categories.map = data.categories.map
			for (let i in data.prods){
				let prod = data.prods[i]
				state.prods.list.push(prod)
				state.prods.show.push(prod)
			}
			state.server.loaded = true
		},
		loadCart: (state, cart) => {
			let prods = state.prods,
				sum = state.server.sum

			for (let i in cart) {
				let item = cart[i],
					prod = prods.list.find(prod => prod.id == item.id)
				sum += item.sum * 1
				if (prod) {
					prod.sum = item.sum
					prod.count = item.count
					prod.package = item.package
					prods.cart.push(prod)
				}
				else {
					prods.list.push(item)
					prods.cart.push(item)
				}
			}

			state.server.sum = sum
		},
		loadProds: (state, data) => {
			state.server.more = data.more
			let prods = state.prods

			if (!state.server.offset) prods.show.splice(0, prods.show.length)

			for (let item of data.prods) {
				let prod = prods.list.find(prod => prod.id == item.id)
				if (prod) prods.show.push(prod)
				else {
					prods.list.push(item)
					prods.show.push(item)
				}
			}
		},
		prodCount: (state, { id, count }) => {
			let prod = state.prods.list.find(item => item.id == id),
				cart = state.prods.cart.find(item => item.id == id)

			prod.count = count > 0 ? count : 0

			state.server.sum -= prod.sum
			prod.sum = prod.count * prod.price * prod[prod.package]
			state.server.sum += prod.sum

			if (prod.count == 0 && cart)
				state.prods.cart.splice(state.prods.cart.indexOf(cart), 1)
			else if (prod.count > 0 && !cart) state.prods.cart.push(prod)
		},
		prodPackage: (state, { id, name }) => {
			let prod = state.prods.list.find(item => item.id == id)

			prod.package = name

			state.server.sum -= prod.sum
			prod.sum = prod.count * prod.price * prod[prod.package]
			state.server.sum += prod.sum
		},
		filter: (state, data) => {
			if (data.type == 'search') state.server.search = data.val
			if (data.type == 'category') state.server.category = data.id
			if (data.type == 'sort') {
				state.server.sort.id = data.id
				state.server.sort.dir = data.dir
			}
			if (data.type == 'more') state.server.offset++
			else state.server.offset = 0
		},
		cookieCart: (state) => {
			if (state.prods.cart.length == 0) {
				document.cookie = 'cart=null;max-age=-1'
				return
			}

			let cart = state.prods.cart,
				arr = []

			for (let i in cart)
				arr[i] = [cart[i].id, cart[i].count * cart[i][cart[i].package]]

			let str = JSON.stringify(arr),
				options = {
					path: '/',
					'max-age': 1200000
				},
				updatedCookie = encodeURIComponent('cart') + "=" + encodeURIComponent(str)

			for (let optionKey in options) {
				updatedCookie += "; " + optionKey
				let optionValue = options[optionKey]
				if (optionValue !== true)
					updatedCookie += "=" + optionValue
			}
			document.cookie = updatedCookie
		}
	},
	actions: {
		calc: ({commit}, data) => {
			if (data.type == 'count') commit('prodCount', data)
			if (data.type == 'package') commit('prodPackage', data)
			commit('cookieCart')
		},
		filter: ({ commit, dispatch }, data) => {
			commit('filter', data)
			dispatch('loadProds')
		},
		loadProds: async ({ state: { server, categories }, commit, dispatch }) => {
			let { prods, more } = await dispatch('server/loadProds', {
					category: server.category,
					path: server.category > 0 ? categories.list[server.category].path : '',
					search: server.search,
					sortId: server.sort.id,
					sortDir: server.sort.dir,
					offset: server.offset
				}, { root: true })

			commit('loadProds', { prods, more })
		},
		loadCatalog: async ({ commit, dispatch, getters, rootGetters }) => {
			commit('loadCatalog', rootGetters['server/catalog'])

			if (!getters.cookieCart) return
			// let cookie = getters.cookieCart.replace(/\[/g, '{')
			// cookie = cookie.replace(/]/g, '}')
			// let { data: cart } = await Axios.post('/api/prods/cart', { cart: cookie })
			let cart = await dispatch('server/loadCart', getters.cookieCart, { root: true })
			commit('loadCart', cart)
		}
	}
}