<template lang="pug">

.first
	v-slider.first-slider(ref="flickity" :options="sliderOptions")
		.first-slider-item
			.first-slider-item-inner
		.first-slider-item
			.first-slider-item-inner
		.first-slider-item
			.first-slider-item-inner

</template>

<script>
import flickity from 'vue-flickity'

export default {
	components: {
		'v-slider': flickity
	},
	data () {
		return {
			sliderOptions: {
				wrapAround: true,
				prevNextButtons: false,
				autoPlay: 5000,
				pauseAutoPlayOnHover: false
			}
		}
	}
}

</script>

<style lang="stylus">

.first
	margin 60px auto 90px
	&-slider
		height 520px
		&-item
			position relative
			height 500px
			width 100%
			margin 10px 0
			&-inner
				width 1080px
				border-radius: 20px
				background-color #fff
				position absolute
				height 100%
				left 50%
				box-shadow 0 0 6px 0 rgba(#000, .1)
				transform: translateX(-50%)

</style>
