<template lang="pug">

.popup.auth
	.toggle
		.toggle-item(:class="{active: panel == 'login'}" @click="panel = 'login'") 
		.toggle-item(:class="{active: panel == 'reg'}" @click="panel = 'reg'") 
	.panel(v-if="panel == 'login'")
		input.input-name(placeholder="tel" name="tel" v-model="tel" @keydown="input")
		input.input-pass(placeholder="pass" name="password" type="password" v-model="password" @keydown="input")
		.send(@click="login") 
		.error(v-if="status === false")   
		.info : asd
		.info : asd
	.panel(v-if="panel == 'reg'")
		input.input-name(placeholder="name" name="name" v-model="name" @keydown="input")
		input.input-name(placeholder="surname" name="surname" v-model="surname" @keydown="input")
		input.input-name(placeholder="email" name="email" v-model="email" @keydown="input")
		input.input-name(placeholder="tel" name="tel" v-model="tel" @keydown="input")
		input.input-pass(placeholder="password" name="password" type="password" v-model="password" @keydown="input")
		.send(@click="reg") 

</template>

<script>
export default {
	data(){
		return {
			panel: 'login',
			tel: '',
			password: '',
			email: '',
			name: '',
			surname: '',
			status: null
		}
	},
	methods: {
		input(e){
			if (e.code == 'Enter')
				if (this.panel == 'login') this.login()
				else if (this.panel == 'reg') this.reg()
		},
		async login(){
			this.status = await this.$store.dispatch('profile/login', { tel: this.tel, password: this.password })
			if (this.status) this.$store.commit('setPopup', null)
		},
		async reg(){
			this.status = await this.$store.dispatch('profile/reg', {
				tel: this.tel,
				password: this.password,
				email: this.email,
				name: this.name,
				surname: this.surname
			})
			if (this.status) this.$store.commit('setPopup', null)
		}
	}
}
</script>

<style lang="stylus" scoped>

.auth
	padding 20px
	.toggle
		margin-bottom 20px
		&-item
			font-size 14px
			color blue
			display inline-block
			width 50%
			text-align center
			cursor pointer
	input
		font-size 14px
		display block
	.send
		font-size 14px
		background-color red
	.error
		font-size 14px
		color red
	.info
		font-size 16px

</style>