<template lang="pug">

.categories(:style="{height: containerHeight}")
	.categories-page(v-for="page in pages" :key="page.id" :class="{hidden: page.hidden, remove: !page.active, main: page.id == 0, active: page.active && !page.hidden}")
		.categories-back(v-if="page.parent" @click="back(page)") {{categories[page.id].name}}
		.categories-item(v-for="(item, id) in page.map" :key="id" @click="open(item, id, page)") {{categories[id].name}}

</template>

<script>
export default {
	mounted(){
		this.containerHeight = this.$el.querySelector('.categories-page.active').offsetHeight + 'px'
	},
	data(){
		return {
			pages: [{
				id: 0,
				map: this.$store.state.catalog.categories.map[0],
				active: 1
			}],
			containerHeight: 0
		}
	},
	methods: {
		back(page){
			page.parent.hidden = 0
			page.active = 0
			this.load(page.parent.id)
		},
		open(map, id, parent){
			this.load(id)
			if (map) {
				if (parent) parent.hidden = 1
				if (this.pages.find(item => item.id == id)) this.pages.find(item => item.id == id).active = 1
				else this.pages.push({map, id, parent, active: 1})
			}
		},
		load(id){
			this.$store.dispatch('catalog/filter', {type: 'category', id})
		}
	},
	computed: {
		map(){
			return this.$store.state.catalog.categories.map
		},
		categories(){
			return this.$store.state.catalog.categories.list
		}
	}
}
</script>

<style lang="stylus">

.categories
	width 230px
	position relative
	background-color #fff
	border-radius 10px
	box-shadow 0 0 6px 0 rgba(#000, .1)
	overflow hidden
	&-page
		padding 10px
		position relative
		left 0
		top 0
		width 100%
		background-color #fff
		transition left .3s
		animation show .3s
		&.main
			animation none
		&.hidden
			left -100%
			position absolute
		&.remove
			left 100%
			position absolute
	&-back
		font-size 14px
		margin-bottom 7px
		cursor pointer
		user-select none
		padding-left 5px
	&-item
		font-size 14px
		cursor pointer
		user-select none
		margin-bottom 5px

@keyframes show
	0%
		left 100%
	100%
		left 0

</style>
