<template lang="pug">

.minicard(:class="{active: active}")
	.minicard-top
		.minicard-name {{prod.name}}
		.minicard-maker {{prod.maker}}
		.minicard-sum {{prod.sum+'' | priceMask}} / {{prod.count}} {{text[prod.package]}}
		.minicard-btn-show(@click="active = !active")
	.minicard-bot
		v-calc(:prod="prod")

</template>

<script>
import iCalc from 'temp/prods/calc.vue'

export default {
	props: ['prod'],
	data () {
		return {
			active: false,
			text: {
				one: '',
				pack: '',
				box: '',
			}
		}
	},
	components: {
		'v-calc': iCalc,
	},
}

</script>

<style lang="stylus">

.minicard
	font-size 14px
	padding 8px 15px
	background-color #fff
	border-bottom 1px solid #eee
	position relative
	display block
	border-radius: 0
	margin 0
	&-top
		display flex
		align-items center
		justify-content: space-between
	&-bot
		height 0
		overflow hidden
		position relative
		align-items center
		justify-content: space-between
		justify-content: center
		display flex
		transition height .3s
	&-name, &-maker, &-weight, &-price, &-amount, &-sum
		font-size 12px
		display inline-block
		line-height 1.8
		width auto
	&-sum
		font-weight 600
		margin-right 25px
	&-btn-show
		background-image url(/img/arrow-down.svg)
		background-repeat no-repeat
		background-size contain
		opacity 0.4
		width 12px
		height 12px
		position absolute
		right 15px
		top 14px
		cursor pointer
	&-nav
		width auto
		justify-content: left
		margin-right -12px
		&-count
			font-size 12px
			width 15px
		&-input
			font-size 12px
			margin: 0 6px
			max-width none
			width 8px
			min-width 8px
		&-units
			margin 0 5px
			&-item
				font-size 12px
				&.active
					padding 0 5px
	&.active
		padding-bottom 5px
		.minicard-btn-show
			transform: rotate(180deg)
			top 14px
		.minicard-bot
			height auto
			overflow visible
			margin-top 5px

</style>
