<template lang="pug">

.calc(:class="{delete: isDelete}")
	.calc-delete   ?
		.calc-delete-no(@click="isDelete = false") 
		.calc-delete-yes(@click="count = 0") 
	.calc-main
		.calc-btn(v-if="count>0" @click="count--") -
		input.calc-input(v-model="count" @input="mask($event.target)")
		.calc-hidden-input
		.calc-btn(@click="count++") +
		.calc-units(:class="{active: active, over: over}")
			.calc-units-container
				.calc-units-inner
					.calc-units-item(v-if="prod.one" @click="changePackage('one')" :class="{active: prod.package == 'one'}") {{text['one']}}
					.calc-units-item(v-if="prod.pack" @click="changePackage('pack')" :class="{active: prod.package == 'pack'}") {{text['pack']}}
						.calc-units-item-info {{prod['pack']}} 
					.calc-units-item(v-if="prod.box" @click="changePackage('box')" :class="{active: prod.package == 'box'}") {{text['box']}}
						.calc-units-item-info {{prod['box']}} 

</template>

<script>

export default {
	mounted: function(){
		if (this.prod.count > 9) this.resizeInput()
	},
	data () {
		return {
			active: 0,
			over: 0,
			isDelete: false,
			text: {
				one: '',
				pack: '',
				box: '',
			}
		}
	},
	methods: {
		changePackage(name){
			if (this.active)
				setTimeout(()=>{
					this.over = 0
				}, 300)
			else this.over = 1
			this.active = !this.active
			if (!this.prod.package[name])
				this.$store.dispatch('catalog/calc', { type: 'package' ,id: this.prod.id, name: name })
		},
		resizeInput(){
			this.input.hid.innerText = this.maskVal(this.prod.count+'')
			this.input.vis.style.width = this.input.hid.offsetWidth + 'px'
		}
	},
	computed:{
		count: {
			get(){
				if (this.$el) this.resizeInput()
				return this.prod.count
			},
			set(val){
				val = this.format(val+'') || 0
				if (this.prod.count == 0 && val <= 0) return
				if (val > 0) this.$store.dispatch('catalog/calc', {type:'count',id: this.prod.id,count: val})
				else if (!this.isDelete) this.isDelete = true
				else {
					this.isDelete = false
					this.$store.dispatch('catalog/calc', {type:'count',id: this.prod.id, count: val})
					this.resizeInput()
				}
			}
		},
		input(){
			return {
				vis: this.$el.querySelector('.calc-input'),
				hid: this.$el.querySelector('.calc-hidden-input')
			}
		}
	},
	props: ['prod']
}

</script>

<style lang="stylus">

.calc
	position relative
	&-delete
		display none
		&-yes
			margin-left 10px
			color red
			cursor pointer
		&-no
			margin-left 10px
			color blue
			cursor pointer
	&-main
		justify-content: flex-end
		display flex
	&.delete
		.calc
			&-delete
				display flex
				align-items center
			&-main
				display none
				opacity 0
	&-input
		height 27px
		font-size 14px
		min-width 9px
		width 10px
		margin 0 10px
		text-align center
		border: 0
		background transparent
		flex-shrink 0
	&-hidden-input
		position absolute
		opacity 0
		z-index -100
		right -100px
		top 0
		font-size 14px
		padding 0 1px
	&-btn
		font-size 17px
		height 27px
		line-height 26px
		width 20px
		cursor pointer
		user-select: none
		text-align center
	&-units
		position relative
		cursor pointer
		user-select: none
		height 33px
		margin-left 0
		margin-top -6px
		margin-right -60px
		&-item
			font-size 13px
			height 27px
			display flex
			justify-content center
			align-items center
			padding 0 5px
			order 1
			position relative
			&-info
				position absolute
				left 100%
				top 2px
				margin-left 3px
				background-color #eee
				border-radius 6px
				box-shadow 0 0 6px 0 rgba(#000, .1)
				height 23px
				padding 0 6px
				line-height 23px
				white-space nowrap
				opacity 0
				transition opacity .1s
			&.active
				order: 0
				&:hover
					.calc-units-item-info
						opacity 1
		&-container
			position relative
			max-height 100%
			overflow hidden
			transition: max-height .3s
			border-radius 3px
			z-index 2
			padding 6px
			padding-right 60px
		&-inner
			transition: background-color .3s, box-shadow .3s
			background-color #fff
			display flex
			flex-direction column
		&.over
			z-index 3
		&.active
			.calc-units
				&-container
					max-height 120px
				&-inner
					background-color #eee
					box-shadow 0 0 6px 0 rgba(#000, .1)
				&-item
					&:hover
						.calc-units-item-info
							opacity 1

</style>
