<template lang="pug">

.list
	.list-nav
		input.list-nav-input(placeholder="" v-model="search")
		.list-nav-sort
			.list-nav-sort-item(v-for="item in sortList" @click="setSort(item.id)" :class="{down: item.id == sort.id && item.defaultDir == sort.dir, up: item.id == sort.id && item.defaultDir != sort.dir}") {{item.name}}
	.list-info
		.list-info-name , , , -  
		.list-info-price 
		.list-info-calc 
	.list-inner
		v-card(v-for="prod in $store.state.catalog.prods.show" :prod="prod" :key="prod.id")
	.list-more(v-if="$store.state.catalog.server.more" @click="loadMore")  

</template>

<script>
import iCard from 'temp/prods/card.vue'

export default {
	mounted(){
		this.searchStr = this.$store.state.catalog.server.search
	},
	data(){
		return {
			sortList: [
				{
					id: 'popular',
					name: '',
					defaultDir: true
				},
				{
					id: 'name',
					name: '',
					defaultDir: false
				},
				{
					id: 'price',
					name: '',
					defaultDir: false
				}
			],
			searchStr: ''
		}
	},
	computed: {
		search: {
			get(){
				return this.searchStr
			},
			set(val){
				this.searchStr = val
				setTimeout(()=>{
					if (this.searchStr != val) return
					this.$store.dispatch('catalog/filter', { type: 'search', val })
				}, 700)
			}
		},
		sort(){
			return this.$store.state.catalog.server.sort
		}
	},
	methods: {
		setSort(id){
			let dir
			if (this.sort.id == id)
				dir = !this.sort.dir
			else dir = this.sortList.find(item => item.id == id).defaultDir

			this.$store.dispatch('catalog/filter', { type: 'sort', id, dir })
		},
		loadMore(){
			this.$store.dispatch('catalog/filter', { type: 'more' })
		}
	},
	props: ['prods'],
	components: {
		'v-card': iCard
	}
}

</script>

<style lang="stylus">

.list
	position relative
	&-nav
		background-color #fff
		box-shadow 0 0 6px 0 rgba(#000, .1)
		margin-bottom 15px
		padding 0 13px
		height 50px
		border-radius 10px
		display flex
		justify-content space-between
		align-items center
		&-input
			font-size 14px
			background-color #f6f6f6
			border 0
			border-radius 6px
			padding 5px 13px
			width 25%
			&::placeholder
				color #888
		&-sort
			display flex
			margin-right 5px
			&-item
				font-size 14px
				margin-left 15px
				cursor pointer
				line-height 1
				position relative
				user-select none
				&.down
					color green
				&.up
					color red
	&-info
		font-size 14px
		display flex
		margin -5px 0 10px
		padding 0 15px
		color #888
		&-name
			width 50%
		&-price
			width 20%
		&-calc
			width 30%
			text-align right
			display none
	&-more
		font-size 14px
		background-color #fff
		box-shadow 0 0 6px 0 rgba(#000, .1)
		padding 5px 9px
		border-radius 4px
		display inline-block
		margin-top 10px
		left 50%
		position relative
		transform translateX(-50%)
		cursor pointer

</style>
