import Vue from 'vue'
import VueRouter from 'vue-router'
import store from 'core/store'
import catalog from 'temp/catalog'
import page404 from 'temp/pages/404.vue'

Vue.use(VueRouter)

const router = new VueRouter({
	// mode: 'history',
	routes: [
		{
			path: '*',
			name: '404',
			component: page404
		},
		{
			path: '/',
			name: 'catalog',
			component: catalog
		},
		{
			path: '/info',
			name: 'info',
			component: () => import('temp/pages/info.vue')
		},
		{
			path: '/contacts',
			name: 'contacts',
			component: () => import('temp/pages/contacts.vue')
		},
		{
			path: '/cart',
			name: 'cart',
			component: () => import('temp/cart/cart.vue')
		},
		{
			path: '/profile',
			component: () => import('temp/profile'),
			beforeEnter: async (to, from, next) => {
				if (await store.getters.auth) next()
				else next('/')
			},
			children: [
				{
					path: '/profile/',
					name: 'profile',
					component: () => import('temp/profile/bonus.vue')
				},
				{
					path: 'personal',
					component: () => import('temp/profile/personal.vue')
				},
				{
					path: 'history',
					component: () => import('temp/profile/history.vue')
				},
				{
					path: 'settings',
					component: () => import('temp/profile/settings.vue')
				},
			]
		},
	],
	scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition
		} else {
			return { x: 0, y: 0 }
		}
	}

})

export default router
