import Vue from 'vue'
import Main from 'core/main.vue'
import router from 'core/router'
import store from 'core/store'
import filters from 'core/plugins/filters'
import format from 'core/plugins/format'

Vue.use(filters)
Vue.use(format)

Vue.config.productionTip = false

new Vue({
	router,
	store,
	render: h => h(Main)
}).$mount('#app')
